----------------------------------------------*/
.timeline {
  list-style: none;
  padding: 20px 0 20px;
  position: relative;
}

.timeline:before {
  top: 0;
  bottom: 0;
  position: absolute;
  content: " ";
  width: 10px;
  background-color: #eaeaea;
  left: 50%;
  margin-left: -5px;
  border-radius: 5px;
}

.timeline > li {
  margin-bottom: 20px;
  position: relative;
}

.timeline > li:before,
.timeline > li:after {
  content: " ";
  display: table;
}

.timeline > li:after {
  clear: both;
}

.timeline > li:before,
.timeline > li:after {
  content: " ";
  display: table;
}

.timeline > li:after {
  clear: both;
}

.timeline > li > .timeline-panel {
  width: 45%;
  float: left;
  border: 1px solid #eaeaea;
  border-radius: 0px;
  padding: 25px;
  position: relative;
  box-shadow: 0 1px 6px rgba(150, 150, 150, 0.175);
  z-index: 11;
  margin-left: -15px;
}

.timeline > li > .timeline-panel:before {
  position: absolute;
  top: 36px;
  right: -15px;
  display: inline-block;
  border-top: 15px solid transparent;
  border-left: 15px solid #eaeaea;
  border-right: 0 solid #eaeaea;
  border-bottom: 15px solid transparent;
  content: " ";
}

.timeline > li > .timeline-panel:after {
  position: absolute;
  top: 37px;
  right: -14px;
  display: inline-block;
  border-top: 14px solid transparent;
  border-left: 14px solid #fff;
  border-right: 0 solid #fff;
  border-bottom: 14px solid transparent;
  content: " ";
}

.timeline > li > .timeline-badge {
  color: #fff;
  width: 70px;
  height: 70px;
  line-height: 58px;
  font-size: 28px;
  text-align: center;
  position: absolute;
  top: 20px;
  left: 50%;
  margin-left: -35px;
  background-color: #7f8ff4;
  z-index: 10;
  border-top-right-radius: 50%;
  border-top-left-radius: 50%;
  border-bottom-right-radius: 50%;
  border-bottom-left-radius: 50%;
  border: 5px solid #eaeaea;
}

